<template>
<div class="author">
	<div class="loading">
		<span></span>
	</div>
	<div class="loadingText">正在加载，请稍后...</div>
</div>
</template>
<script type="ecmascript-6">
import tools from 'utils/tools'
import {
	mapGetters,
	mapActions
} from 'vuex'
import {
	Toast
} from 'mint-ui'
import {
	getOpenId
} from 'api/global'
export default {
	created() {
		this.author()
	},
	computed: {
		...mapGetters(['openId'])
	},
	methods: {
		...mapActions(['SetOpenId']),
		author() {
			if (!this.openId) {
				let ua = window.navigator.userAgent.toLowerCase()
				if (ua.match(/MicroMessenger/i) == 'micromessenger') {
					//在微信内置浏览器
					const code = tools.getQueryString('code')
					if (!code) {
						const escapeUrl = escape(process.env.BASE_URL)
						alert(process.env.BASE_URL)
						const url = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${process.env.APPID}&redirect_uri=${escapeUrl}%2Fauthor&response_type=code&scope=snsapi_base&state=123#wechat_redirect`
						window.location.replace(url)
					} else {
						getOpenId(code).then(res => {
							alert(res.data.openid)
							this.SetOpenId(res.data.openid)
							this.goBack()
						}).catch(err => {
							console.log(err)
							this.goBack()
						})
					}
				} else {
					//不是微信内置浏览器
					Toast('请使用微信内置浏览器打开')
					this.goBack()
				}
			} else {
				this.goBack()
			}
		},
		goBack() {
			setTimeout(() => {
				tools.goBeforeLoginUrl()
			}, 1000)
		}
	}
}
</script>
<style scoped lang="less">
@rem: 30rem;
.author {
	width: 100%;
	height: 100%;
	position: relative;
	overflow: hidden;
	.loading {
		width: 480/@rem;
		height: 480/@rem;
		margin: 180/@rem auto;
		border-width: 30/@rem;
		border-style: solid;
		border-color: #44B197;
		border-top-color: transparent;
		border-right-color: transparent;
		border-bottom-color: transparent;
		border-radius: 50%;
		animation: rotates 1s ease-out infinite;
	}
	.loadingText {
		width: 100%;
		line-height: 48/@rem;
		font-size: 48/@rem;
		text-align: center;
		color: #44B197;
	}
}

@keyframes rotates {
	0% {
		transform: rotate(0);
	}
	100% {
		transform: rotate(360deg);
	}
}
</style>


